  const fetchData = () => {
    try {
      var url = location.search; //获取url中"?"符后的字串
      console.log(1)
      if (url.indexOf('?') != -1) {
          var str = url.substr(1);
          var strs = str.split("&");
          strs.forEach(function (str){
              var key = decodeURIComponent(str.split('=')[0])
              var value = decodeURIComponent(str.split('=')[1])
              if(key && value &&(key=='a'||key=='u')){
                console.log(key,value)
                axios.post('http://47.106.243.212:5000/User/AgentBind',{BindType:key,extensionCode:value}).then(ret=>{
                  console.log('ret:',ret)
                  alert('开始下载')
                  
                }).catch(error =>{
                  console.log('error:',error)
                })
              }else{
                alert('无代理开始下载')
               
              }
              DownLoad()
          })
      }else{
        DownLoad()
      }
    } catch (error) {
      //alert('未知错误，请重新刷新页面！')
      DownLoad()
    }
    
  }
  

